<template>
    <div class="container">

        <b-container class="bv-example-row">
            <b-row>
                <b-col>
                    <h2>Daily Tasks</h2>
                </b-col>
            </b-row>
            <b-row>
                <b-col>
                    <h5>Progress: {{completedPerc}}%</h5>
                    <b-progress class="progressBar" :value="completedPerc" show-progress animated></b-progress>
                </b-col>
            </b-row>
            <b-row class="inputRow">
                <b-col>
                    <b-form-input v-model="newTodoItem" placeholder="Enter new task"  @keyup.enter="addNewItem"></b-form-input>
                </b-col>
                <b-col cols="2">
                    <b-button variant="success" @click="addNewItem">Add new</b-button>
                </b-col>
            </b-row>


            <b-row>
                <b-col>
                    <b-card
                            title="Task list"
                            tag="article"
                            class="mb-2"
                    >
                        <b-card-text>
                            <div v-if="this.todoTasks.length === 0">No tasks exist</div>
                            <div v-for="(todoItem, index) in this.todoTasks" :key="todoItem.id">
                                <b-row class="todoItem">
                                    <b-col cols="1" class="todoLabel">
                                        {{index+1}}
                                    </b-col>

                                    <b-col cols="1">
                                        <b-form-checkbox
                                                class="todoCheckbox"
                                                v-model="todoItem.completed"
                                        >
                                        </b-form-checkbox>
                                    </b-col>

                                    <b-col class="colLabel">
                        <span class="todoLabel" :class="{ completedItem : todoItem.completed }">
                            <b-badge class="badge" @click="incrementLevel(index)" pill v-b-tooltip.hover title="Level" :class="{ badgeGreen : todoItem.level === 1, badgeYellow : todoItem.level === 2, badgeRed : todoItem.level === 3}">{{todoItem.level}}</b-badge>
                                {{todoItem.name}}
                            </span>
                                    </b-col>
                                    <b-col cols="3">
                                        <b-button class="todoBtn" variant="danger" @click="removeItem(index)">Delete</b-button>
                                    </b-col>
                                </b-row>
                            </div>
                        </b-card-text>
                        <b-button  v-if="completedTasks.length > 0" @click="clearCompleted()" variant="primary">Clear completed</b-button>
                    </b-card>
                </b-col>
            </b-row>

        </b-container>
    </div>
</template>

<script>
    export default {
        name: 'Todo',
        props: {

        },
        data()  {
                return {
                    newTodoItem: "",
                    idCounter: 2,
                    todoTasks: [
                        {
                            id: 0,
                            name: "Wake up",
                            completed: true,
                            level: 1
                        },{
                            id: 1,
                            name: "Make breakfast",
                            completed: false,
                            level: 2
                        },
                    ]
            }
        },
        methods: {
            addNewItem(){
                if(this.newTodoItem.trim().length === 0){
                    return;
                }
                this.todoTasks.push({
                    id: this.idCounter,
                    name: this.newTodoItem,
                    completed: false,
                    level: 1
                });
                this.newTodoItem = "";
                this.idCounter++;
            },
            removeItem(index) {
                this.todoTasks.splice(index, 1)
            },
            clearPending(){
                this.todoTasks = this.completedTasks;
            },
            clearCompleted(){
                this.todoTasks = this.pendingTasks;
            },
            incrementLevel(index){
                if(this.todoTasks[index].level < 3){
                    this.todoTasks[index].level++;
                }else{
                    this.todoTasks[index].level = 1
                }

            }
        },
        computed:{
            completedTasks: function () {
                return this.todoTasks.filter((task) => {
                    if(task.completed){
                        return task
                    }else{
                        return null
                    }
                })
            },
            pendingTasks: function () {
                return this.todoTasks.filter((task) => {
                    if(!task.completed){
                        return task
                    }else{
                        return null
                    }
                })
            },
            completedPerc: function (){

                if(this.todoTasks.length === 0){
                    return 0.00;
                }
                let compl = 0;
                this.todoTasks.forEach((item) => {
                    if(item.completed){
                        compl ++;
                    }
                });
                let perc = (compl / this.todoTasks.length) * 100;

                return perc.toFixed(0);
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .todoLabel {
        font-size: 26px;
    }
    .todoCheckbox{
        margin: 10px 0
    }
    .todoItem{
        border-bottom: 1px #eeeeee solid;
    }
    .todoBtn{
        margin: 5px 0
    }
    .completedItem{
        color: #eeeeee;
        text-decoration: line-through;
    }
    .colLabel{
        text-align: left;
    }

    .progressBar{
        margin: 15px 0 20px;
    }
    .inputRow{
        margin-bottom: 20px;
    }
    .badgeGreen{
        background-color: #4caf50;
    }
    .badgeYellow{
        background-color: #ffc107;
    }
    .badgeRed{
        background-color: #f4511e;
    }
    .badge{
        cursor: pointer;
    }
</style>
