<template>
    <div class="hello">
        <div class="container">
            <b-container class="bv-example-row">

                <b-row align-h="center">
                    <b-col sm="12" md="8" lg="6">
                        <b-card

                                title="Login"
                                class="mb-2 loginCard"
                        >
                            <b-card-text>
                                <b-form @submit="onSubmit" >
                                    <b-form-group
                                            id="input-group-1"
                                            label="Email address:"
                                            label-for="input-1"
                                    >
                                        <b-form-input
                                                id="input-1"
                                                v-model="form.email"
                                                type="email"
                                                placeholder="Enter email"
                                                required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group id="input-group-2" label="Your Password:" label-for="input-2"
                                                  description="Hint: try testuser@test with password test"
                                    >
                                        <b-form-input
                                                id="input-2"
                                                v-model="form.password"
                                                placeholder="Enter password"
                                                type="password"
                                                required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-button type="submit" variant="primary">Login</b-button>
                                </b-form>
                            </b-card-text>
                            <span class="errorMessage" v-if="showError"> Error! Invalid password or username.</span>
                        </b-card>
                    </b-col>
                </b-row>

            </b-container>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'Login',
        data() {
            return {
                form: {
                    email: '',
                    password: ''

                },
                showError: false
            }
        },
        methods: {
            onSubmit(event) {
                event.preventDefault();
                this.showError = this.form.email !== 'testuser@test' || this.form.password !== "test";
                if(!this.showError){
                    this.$parent.login()
                }
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .loginCard{
        margin-top: 30px;
    }
    .errorMessage{
        color: red
    }
</style>
